img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@font-face {
  font-family: "Oswald";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-weight: bold;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
.page {
  height: 100%;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
}

.page__body {
  background-color: #ffffff;
  color: #444444;
  min-width: 320px;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow-x: hidden;
}

.container {
  width: auto;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .container {
    width: auto;
    padding: 0 30px;
  }
}
@media (min-width: 1440px) {
  .container {
    width: 1220px;
    margin: 0 auto;
    padding: 0 110px;
  }
}

@media (min-width: 768px) {
  .container--form {
    padding: 0 65px 0 70px;
  }
}
@media (min-width: 1440px) {
  .container--form {
    padding: 0 110px;
  }
}

@media (min-width: 1440px) {
  .container--user,
  .container--goal {
    padding: 0;
    width: auto;
  }
}

.page-header {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #ffffff;
  padding-top: 12px;
  padding-bottom: 12px;
}
@media (min-width: 768px) {
  .page-header {
    padding-top: 23px;
    padding-bottom: 23px;
  }
}
@media (min-width: 1440px) {
  .page-header {
    background-color: transparent;
    padding-top: 55px;
  }
}

.navigation {
  position: absolute;
  left: 0;
  top: 65px;
  width: 100%;
  z-index: 100;
}
@media (min-width: 768px) {
  .navigation {
    position: static;
    width: auto;
  }
}
.navigation--visible {
  height: 100vh;
}

.navigation__overlay {
  background-color: rgba(31, 27, 22, 0.24);
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity 0.3s;
}
@media (min-width: 768px) {
  .navigation__overlay {
    display: none;
  }
}

.navigation--visible .navigation__overlay {
  opacity: 1;
}

.burger {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 20px;
  background-image: url("../image/btn_ham.svg");
  background-repeat: no-repeat;
  background-position: center;
  transition: background 0.3s;
}
@media (min-width: 768px) {
  .burger {
    display: none;
  }
}
.burger--active {
  background-image: url("../image/icon_close.svg");
}
@media (min-width: 768px) {
  .burger--active {
    display: none;
  }
}

.navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: none;
  position: absolute;
  top: 0;
  width: 100%;
  flex-direction: column;
  background-color: #ffffff;
  transform: translateX(100%);
  transition: transform 0.6s;
  z-index: 1000;
}
@media (min-width: 768px) {
  .navigation__list {
    display: flex;
    position: static;
    flex-direction: row;
    flex-wrap: wrap;
    background-color: transparent;
    transform: none;
  }
}

.navigation--visible > .navigation__list {
  display: flex;
  transform: none;
}

.navigation__item {
  padding-top: 22px;
  padding-bottom: 21px;
  text-align: center;
  border-top: 1px solid #ebebeb;
}
.navigation__item:last-child {
  border-bottom: 1px solid #ebebeb;
}
@media (min-width: 768px) {
  .navigation__item {
    border: none;
    margin-right: 34px;
  }
  .navigation__item:last-child {
    border-bottom: none;
    margin: 0;
  }
}

.navigation__link {
  text-decoration: none;
  font-weight: bold;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  color: #000000;
}
@media (min-width: 768px) {
  .navigation__link {
    font-size: 18px;
    line-height: 24px;
    font-weight: normal;
    padding-bottom: 6px;
  }
}
@media (min-width: 1440px) {
  .navigation__link {
    font-size: 20px;
    line-height: 30px;
    font-weight: normal;
  }
}
@media (min-width: 1440px) {
  .navigation__link--index {
    color: #ffffff;
  }
}
@media (min-width: 768px) {
  .navigation__link--current {
    border-bottom: 2px solid #68b738;
  }
}
@media (min-width: 1440px) {
  .navigation__link--index--current {
    border-bottom: 2px solid #ffffff;
  }
}

.navigation__link[href]:hover {
  opacity: 0.6;
}

.navigation__link[href]:active {
  opacity: 0.3;
}

.no-scroll {
  overflow: hidden;
}

.button {
  background-color: transparent;
  border: none;
}

